// components/Charts/ExpenseTypeChart.js
import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';

export const ExpenseTypeChart = ({ data }) => {
  // Lọc và sắp xếp dữ liệu theo giá trị giảm dần
  const chartData = Object.entries(data)
    .map(([type, stats]) => ({
      name: type,
      y: stats.amount
    }))
    .sort((a, b) => b.y - a.y);

  const options = {
    chart: {
      type: 'column',
      backgroundColor: 'transparent',
    },
    title: null,
    xAxis: {
      type: 'category',
      labels: {
        rotation: -45,
        style: {
          fontSize: '11px',
          fontWeight: 'normal',
          color: '#666'
        }
      }
    },
    yAxis: {
      title: {
        text: 'Giá trị (₫)',
        style: {
          color: '#666'
        }
      },
      labels: {
        formatter: function() {
          return Highcharts.numberFormat(this.value, 0, ',', '.') + ' ₫';
        }
      }
    },
    legend: {
      enabled: false
    },
    tooltip: {
      pointFormatter: function() {
        return 'Giá trị: <b>' + Highcharts.numberFormat(this.y, 0, ',', '.') + ' ₫</b>';
      }
    },
    plotOptions: {
      column: {
        borderRadius: 5,
        colors: ['#36A2EB'], // Màu xanh dương như trong hình
        colorByPoint: false
      }
    },
    series: [{
      name: 'Chi phí',
      data: chartData,
      color: '#36A2EB',
      dataLabels: {
        enabled: true,
        rotation: 0,
        color: '#666',
        align: 'center',
        formatter: function() {
          return Highcharts.numberFormat(this.y, 0, ',', '.') + ' ₫';
        },
        y: -25,
        style: {
          fontSize: '10px',
          fontWeight: 'normal',
          textOutline: 'none'
        }
      }
    }]
  };

  return <HighchartsReact highcharts={Highcharts} options={options} />;
};