
// src/components/Filters/DateRangeFilter.js
import React from 'react';

export const DateRangeFilter = ({ title, fromDate, toDate, onChange }) => {
  return (
    <div className="filter-group p-4 rounded-lg border border-gray-200 dark:border-gray-700">
      <div className="mb-3">
        <label className="text-sm font-medium text-gray-700 dark:text-gray-300">
          {title}
        </label>
      </div>
      
      <div className="space-y-3">
        <div>
          <label className="text-xs text-gray-600 dark:text-gray-400">Từ ngày</label>
          <input
            type="date"
            value={fromDate}
            onChange={(e) => onChange({ fromDate: e.target.value, toDate })}
            className="w-full mt-1 bg-gray-50 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg py-2 px-3 text-sm"
          />
        </div>
        
        <div>
          <label className="text-xs text-gray-600 dark:text-gray-400">Đến ngày</label>
          <input
            type="date"
            value={toDate}
            onChange={(e) => onChange({ fromDate, toDate: e.target.value })}
            className="w-full mt-1 bg-gray-50 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg py-2 px-3 text-sm"
          />
        </div>
      </div>
    </div>
  );
};
