import React from 'react';
import { formatCurrency, formatDate } from '../../utils/formatUtils';

const TableHeader = ({ onSort, sortColumn, sortDirection }) => {
  const headers = [
    { key: 'Ngày giải ngân', label: 'Ngày giải ngân' },
    { key: 'Thời gian', label: 'Ngày tạo' },
    { key: 'KHU VỰC', label: 'Khu vực' },
    { key: 'Dự án', label: 'Dự án' },
    { key: 'Mã CHI', label: 'Mã CHI' },
    { key: 'SỐ TIỀN', label: 'Số tiền' },
    { key: 'NHÂN VIÊN THỰC HIỆN', label: 'Nhân viên' },
    { key: 'NỘI DUNG', label: 'Nội dung' }
  ];

  return (
    <thead className="bg-gray-50 dark:bg-gray-700">
      <tr>
        {headers.map(header => (
          <th
            key={header.key}
            onClick={() => onSort(header.key)}
            className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider cursor-pointer"
          >
            {header.label}
            {sortColumn === header.key && (
              <i className={`fas fa-sort-${sortDirection} ml-1`} />
            )}
          </th>
        ))}
      </tr>
    </thead>
  );
};

const TableBody = ({ data }) => {
  return (
    <tbody className="bg-white dark:bg-gray-800 divide-y divide-gray-200 dark:divide-gray-700">
      {data.map((item, index) => (
        <tr
          key={index}
          className="hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors duration-150"
        >
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["Ngày giải ngân"] ? formatDate(item["Ngày giải ngân"]) : '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["Thời gian"] ? formatDate(item["Thời gian"]) : '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["KHU VỰC"] || '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["Dự án"] || '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["Mã CHI"] || '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["SỐ TIỀN"] ? formatCurrency(parseFloat(item["SỐ TIỀN"])) : '-'}
          </td>
          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300">
            {item["NHÂN VIÊN THỰC HIỆN"] || '-'}
          </td>
          <td className="px-6 py-4 text-sm text-gray-900 dark:text-gray-300">
            {item["NỘI DUNG"] || '-'}
          </td>
        </tr>
      ))}
    </tbody>
  );
};

const Pagination = ({
  currentPage,
  pageSize,
  totalItems,
  onPageChange,
  onPageSizeChange
}) => {
  const totalPages = Math.ceil(totalItems / pageSize);
  const pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);

  return (
    <div className="flex items-center justify-between mt-4">
      <div className="flex items-center space-x-2 text-sm text-gray-500 dark:text-gray-400">
        <select
          value={pageSize}
          onChange={(e) => onPageSizeChange(Number(e.target.value))}
          className="bg-white dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg px-2 py-1"
        >
          {[10, 20, 50, 100].map(size => (
            <option key={size} value={size}>
              {size} dòng
            </option>
          ))}
        </select>
        <span>
          Hiển thị {Math.min((currentPage - 1) * pageSize + 1, totalItems)} - {Math.min(currentPage * pageSize, totalItems)} của {totalItems} kết quả
        </span>
      </div>

      <div className="flex items-center space-x-2">
        <button
          onClick={() => onPageChange(currentPage - 1)}
          disabled={currentPage === 1}
          className="px-3 py-1 rounded border border-gray-300 dark:border-gray-600 text-sm disabled:opacity-50"
        >
          <i className="fas fa-chevron-left" />
        </button>

        {pageNumbers.map(number => (
          <button
            key={number}
            onClick={() => onPageChange(number)}
            className={`px-3 py-1 rounded border ${
              currentPage === number
                ? 'bg-indigo-600 text-white'
                : 'border-gray-300 dark:border-gray-600'
            } text-sm`}
          >
            {number}
          </button>
        ))}

        <button
          onClick={() => onPageChange(currentPage + 1)}
          disabled={currentPage === totalPages}
          className="px-3 py-1 rounded border border-gray-300 dark:border-gray-600 text-sm disabled:opacity-50"
        >
          <i className="fas fa-chevron-right" />
        </button>
      </div>
    </div>
  );
};

export const DataTable = ({
  data,
  currentPage,
  pageSize,
  totalItems,
  sortColumn,
  sortDirection,
  onSort,
  onPageChange,
  onPageSizeChange
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-xl shadow-lg overflow-hidden">
      <div className="p-6">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
            Chi tiết chi phí
          </h3>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
            <TableHeader 
              onSort={onSort}
              sortColumn={sortColumn}
              sortDirection={sortDirection}
            />
            <TableBody data={data} />
          </table>
        </div>

        <Pagination
          currentPage={currentPage}
          pageSize={pageSize}
          totalItems={totalItems}
          onPageChange={onPageChange}
          onPageSizeChange={onPageSizeChange}
        />
      </div>
    </div>
  );
};