// src/utils/exportUtils.js
import * as XLSX from 'xlsx';
import { formatCurrency, formatDateForExport } from './formatUtils';

export const exportToExcel = (data) => {
  if (!Array.isArray(data) || data.length === 0) {
    alert('Không có dữ liệu để xuất');
    return;
  }

  try {
    // Chuẩn bị dữ liệu để xuất
    const exportData = data.map(item => {
      try {
        return {
          'Ngày giải ngân': formatDateForExport(item["Ngày giải ngân"]),
          'Thời gian': formatDateForExport(item["Thời gian"]),
          'Khu vực': item["KHU VỰC"] || '',
          'Dự án': item["Dự án"] || '',
          'Mã CHI': item["Mã CHI"] || '',
          'Số tiền': item["SỐ TIỀN"] ? parseFloat(item["SỐ TIỀN"]) : 0,
          'Nhân viên': item["NHÂN VIÊN THỰC HIỆN"] || '',
          'Nội dung': item["NỘI DUNG"] || '',
          'Trạng thái': item["TRẠNG THÁI"] || ''
        };
      } catch (error) {
        console.error('Lỗi khi xử lý dòng dữ liệu:', error, item);
        return null;
      }
    }).filter(Boolean);

    // Tính tổng
    const totalAmount = data.reduce((sum, item) => {
      const amount = item["SỐ TIỀN"] ? parseFloat(item["SỐ TIỀN"]) : 0;
      return sum + amount;
    }, 0);

    // Thêm dòng tổng
    exportData.push({
      'Ngày giải ngân': 'TỔNG CỘNG',
      'Thời gian': '',
      'Khu vực': '',
      'Dự án': '',
      'Mã CHI': '',
      'Số tiền': totalAmount,
      'Nhân viên': '',
      'Nội dung': '',
      'Trạng thái': ''
    });

    // Tạo worksheet
    const ws = XLSX.utils.json_to_sheet(exportData);

    // Định dạng cột
    ws['!cols'] = [
      { wch: 20 }, // Ngày giải ngân
      { wch: 20 }, // Thời gian
      { wch: 15 }, // Khu vực
      { wch: 20 }, // Dự án
      { wch: 15 }, // Mã CHI
      { wch: 20 }, // Số tiền
      { wch: 20 }, // Nhân viên
      { wch: 50 }, // Nội dung
      { wch: 15 }  // Trạng thái
    ];

    // Style cho dòng tổng
    const lastRow = exportData.length;
    ['A', 'F'].forEach(col => {
      if (!ws[`${col}${lastRow}`]) {
        ws[`${col}${lastRow}`] = { v: '', s: {} };
      }
      ws[`${col}${lastRow}`].s = {
        font: { bold: true },
        fill: { fgColor: { rgb: "FFEB9C" } }
      };
    });

    // Tạo workbook và lưu file
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Chi phí");
    
    const fileName = `Bao_cao_chi_phi_${new Date().toLocaleDateString('vi-VN').replace(/\//g, '_')}.xlsx`;
    XLSX.writeFile(wb, fileName);

  } catch (error) {
    console.error('Lỗi khi xuất Excel:', error);
    alert('Có lỗi xảy ra khi xuất file Excel. Vui lòng thử lại.');
  }
};