// components/Charts/AreaChart.js
import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';

export const AreaChart = ({ data }) => {
  const chartData = Object.entries(data)
    .map(([area, stats]) => ({
      name: area,
      y: stats.amount,
    }))
    .sort((a, b) => b.y - a.y);

  const options = {
    chart: {
      type: 'pie',
      backgroundColor: 'transparent',
    },
    title: null,
    tooltip: {
      pointFormat: 'Tỷ lệ: <b>{point.percentage:.1f}%</b><br>Giá trị: <b>{point.y:,.0f} ₫</b>',
    },
    accessibility: {
      point: {
        valueSuffix: '%',
      },
    },
    colors: ['#36A2EB', '#4B3EDE', '#4BC0C0', '#FF6384'], // Màu xanh dương, tím, xanh lá, đỏ
    plotOptions: {
      pie: {
        allowPointSelect: true,
        cursor: 'pointer',
        dataLabels: {
          enabled: true,
          distance: 20,
          format: '{point.name}: {point.percentage:.1f} %',
          style: {
            fontWeight: 'normal',
            color: '#666',
            textOutline: 'none'
          }
        },
        showInLegend: false
      },
    },
    series: [{
      name: 'Khu vực',
      colorByPoint: true,
      data: chartData,
      size: '80%'
    }],
  };

  return <HighchartsReact highcharts={Highcharts} options={options} />;
};