// api/expenseApi.js
const APP_ID = '1a8d9270-a4ff-46b8-94fc-db1e4632f273';
const ACCESS_KEY = 'V2-IEkJl-GKXgi-aBjtl-fN3Ur-QQSYX-Ggn6e-m9pkA-GtxEU';
const REGION = 'www';

export const fetchExpenseData = async () => {
  const apiUrl = `https://${REGION}.appsheet.com/api/v2/apps/${APP_ID}/tables/Chi/Action`;
  
  try {
    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'ApplicationAccessKey': ACCESS_KEY,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        Action: 'Find',
        Properties: {
          Selector: `Filter(Chi, true)`
        }
      })
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    if (!Array.isArray(data)) {
      throw new Error("Invalid data received from API");
    }

    return data;
  } catch (error) {
    console.error("API Error:", error);
    throw error;
  }
};