
// Sidebar.js
import React from 'react';
import { CategoryFilter, DateRangeFilter } from '../Filters';

export const Sidebar = ({
  isOpen,
  onClose,
  filters,
  onFilterChange,
  data
}) => {
  const handleReset = () => {
    const defaultFilters = {
      dateRange: {
        fromDate: '',
        toDate: ''
      },
      trangThai: [],
      khuVuc: [],
      duAn: [],
      maChi: [],
      nhanVien: []
    };
    onFilterChange(defaultFilters);
  };

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose?.();
    }
  };

  return (
    <>
      <aside
        className={`fixed left-0 top-16 bottom-0 w-80 bg-white shadow-xl transform 
          ${isOpen ? 'translate-x-0' : '-translate-x-full'} 
          lg:translate-x-0 transition-transform duration-200 ease-in-out z-30`}
      >
        <div className="absolute top-0 bottom-[160px] left-0 right-0 overflow-y-auto custom-scrollbar">
          <div className="p-6 space-y-6">
            {/* Mobile close button */}
            <div className="flex items-center justify-between mb-6 lg:hidden">
              <h2 className="text-xl font-semibold text-gray-900">
                Bộ lọc
              </h2>
              <button
                onClick={onClose}
                className="p-2 hover:bg-gray-100 rounded-full"
                aria-label="Close sidebar"
              >
                <i className="fas fa-times text-gray-500" />
              </button>
            </div>

            {/* Desktop title */}
            <div className="hidden lg:block mb-6">
              <h2 className="text-xl font-semibold text-gray-900">
                Bộ lọc
              </h2>
            </div>

            <DateRangeFilter
              title="Khoảng thời gian"
              fromDate={filters.dateRange?.fromDate || ''}
              toDate={filters.dateRange?.toDate || ''}
              onChange={(dateRange) =>
                onFilterChange({ ...filters, dateRange })
              }
            />

            <CategoryFilter
              title="Trạng thái"
              name="trangThai"
              data={[...new Set(data.map((item) => item["TRẠNG THÁI"]))].filter(Boolean).sort()}
              selected={filters.trangThai || []}
              onChange={(selected) =>
                onFilterChange({ ...filters, trangThai: selected })
              }
            />

            <CategoryFilter
              title="Khu vực"
              name="khuVuc"
              data={[...new Set(data.map((item) => item["KHU VỰC"]))].filter(Boolean).sort()}
              selected={filters.khuVuc || []}
              onChange={(selected) =>
                onFilterChange({ ...filters, khuVuc: selected })
              }
            />

            <CategoryFilter
              title="Nhân viên"
              name="nhanVien"
              data={[...new Set(data.map((item) => item["NHÂN VIÊN THỰC HIỆN"]))].filter(Boolean).sort()}
              selected={filters.nhanVien || []}
              onChange={(selected) =>
                onFilterChange({ ...filters, nhanVien: selected })
              }
            />
          </div>
        </div>

        <div className="absolute bottom-0 left-0 right-0 bg-white border-t border-gray-200 p-4 space-y-3">
          <button
            onClick={() => onFilterChange(filters)}
            className="w-full flex items-center justify-center px-4 py-3 space-x-3 bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg shadow-lg transition-colors"
          >
            <i className="fas fa-filter" />
            <span>Áp dụng bộ lọc</span>
          </button>

          <button
            onClick={handleReset}
            className="w-full flex items-center justify-center px-4 py-3 space-x-2 bg-white hover:bg-gray-50 text-gray-700 rounded-lg shadow-lg border border-gray-200 transition-colors"
          >
            <i className="fas fa-undo" />
            <span>Đặt lại</span>
          </button>
        </div>
      </aside>

      {/* Overlay */}
      {isOpen && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-20 lg:hidden"
          onClick={handleOverlayClick}
          aria-hidden="true"
        />
      )}
    </>
  );
};