// components/Charts/ChartContainer.js
import React from 'react';
import { AreaChart } from './AreaChart';
import { ExpenseTypeChart } from './ExpenseTypeChart';

export const ChartContainer = ({ data }) => {
  const calculateStats = (field) => {
    return data.reduce((acc, item) => {
      const key = item[field];
      if (!acc[key]) {
        acc[key] = {
          count: 0,
          amount: 0
        };
      }
      acc[key].count++;
      acc[key].amount += parseFloat(item["SỐ TIỀN"] || 0);
      return acc;
    }, {});
  };

  const areaStats = calculateStats("KHU VỰC");
  const expenseTypeStats = calculateStats("Mã CHI");

  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mt-6">
      <AreaChart data={areaStats} />
      <ExpenseTypeChart data={expenseTypeStats} />
    </div>
  );
};