// src/components/MainContent/index.js
import React, { useEffect, useState } from 'react';
import { StatsContainer } from '../Stats/StatsContainer';
import { ChartContainer } from '../Charts/ChartContainer';
import { DataTable } from '../DataTable/Index';
import { exportToExcel } from '../../utils/exportUtils';

export const MainContent = ({ 
  data,
  currentPage,
  pageSize,
  sortColumn,
  sortDirection,
  onSort,
  onPageChange,
  onPageSizeChange,
  totalItems
}) => {
  const [paginatedData, setPaginatedData] = useState([]);

  useEffect(() => {
    if (Array.isArray(data)) {
      const startIndex = (currentPage - 1) * pageSize;
      const endIndex = startIndex + pageSize;
      setPaginatedData(data.slice(startIndex, endIndex));
    }
  }, [data, currentPage, pageSize]);

  const handleExport = () => {
    if (Array.isArray(data) && data.length > 0) {
      exportToExcel(data);
    } else {
      alert('Không có dữ liệu để xuất');
    }
  };

  return (
    <main className="flex-1 lg:ml-80 p-4 bg-gray-50">
      <div className="max-w-7xl mx-auto space-y-4">
        <div className="bg-white shadow-sm rounded-lg p-4">
          <StatsContainer data={data} />
        </div>
        
        <div className="bg-white shadow-sm rounded-lg p-4">
          <ChartContainer data={data} />
        </div>
        
        <div className="bg-white shadow-sm rounded-lg p-4">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-3 sm:gap-4 mb-4">
            <h2 className="text-base sm:text-lg font-medium text-gray-900">
              Chi tiết chi phí
            </h2>
            <button
              onClick={handleExport}
              className="w-full sm:w-auto px-3 py-1.5 bg-emerald-600 hover:bg-emerald-700 text-white rounded-lg transition-colors duration-200 text-sm flex items-center justify-center"
            >
              <i className="fas fa-file-excel mr-2" />
              Xuất Excel
            </button>
          </div>

          <div className="border-t border-gray-200 pt-4">
            <DataTable
              data={paginatedData}  
              currentPage={currentPage}
              pageSize={pageSize}
              totalItems={totalItems}
              sortColumn={sortColumn}
              sortDirection={sortDirection}
              onSort={onSort}
              onPageChange={onPageChange}
              onPageSizeChange={onPageSizeChange}
            />
          </div>
        </div>
      </div>
    </main>
  );
};