import React, { useState, useCallback, useEffect, useMemo } from 'react';
import { Sidebar } from './components/Sidebar';
import { Header } from './components/Header';
import { MainContent } from './components/MainContent';
import { LoadingSpinner } from './components/LoadingSpinner';
import { ErrorBoundary } from './components/ErrorBoundary';
import { fetchExpenseData } from './api/expenseApi';
import { isDateInRange } from './utils/formatUtils';

const DEFAULT_FILTERS = {
  dateRange: {
    type: 'custom',
     fromDate: null,  // Changed to null instead of new Date()
    toDate: null,    // Changed to null instead of new Date()
  },
  khuVuc: [],
  duAn: [],
  maChi: [],
  nhanVien: [],
  trangThai: []
};

const DEFAULT_PAGE_SIZE = 10;
const DEFAULT_SORT_COLUMN = 'Ngày giải ngân';

const App = () => {
  // State management
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [expenseData, setExpenseData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(DEFAULT_PAGE_SIZE);
  const [sortColumn, setSortColumn] = useState(DEFAULT_SORT_COLUMN);
  const [sortDirection, setSortDirection] = useState('desc');
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [filters, setFilters] = useState(DEFAULT_FILTERS);

  // Memoized filter function
  const applyFilters = useCallback((currentFilters, data) => {
    if (!Array.isArray(data)) return [];

    return data.filter(item => {
      const dateRange = currentFilters.dateRange || {};
      const matchesDate = isDateInRange(
        item["Ngày giải ngân"],
        dateRange.fromDate,
        dateRange.toDate
      );

      const matchesKhuVuc = !currentFilters.khuVuc?.length || 
        (item["KHU VỰC"] && currentFilters.khuVuc.includes(item["KHU VỰC"]));
      
      const matchesTrangThai = !currentFilters.trangThai?.length || 
        (item["TRẠNG THÁI"] && currentFilters.trangThai.includes(item["TRẠNG THÁI"]));
      
      const matchesNhanVien = !currentFilters.nhanVien?.length || 
        (item["NHÂN VIÊN THỰC HIỆN"] && currentFilters.nhanVien.includes(item["NHÂN VIÊN THỰC HIỆN"]));
      
      const matchesDuAn = !currentFilters.duAn?.length || 
        (item["DỰ ÁN"] && currentFilters.duAn.includes(item["DỰ ÁN"]));
      
      const matchesMaChi = !currentFilters.maChi?.length || 
        (item["MÃ CHI"] && currentFilters.maChi.includes(item["MÃ CHI"]));

      return matchesDate && matchesKhuVuc && matchesTrangThai && 
             matchesNhanVien && matchesDuAn && matchesMaChi;
    });
  }, []);

  // Memoized sort function
  const applySorting = useCallback((data, column, direction) => {
    return [...data].sort((a, b) => {
      const aValue = a[column];
      const bValue = b[column];

      if (aValue === bValue) return 0;
      if (aValue === null || aValue === undefined) return 1;
      if (bValue === null || bValue === undefined) return -1;

      const comparison = aValue < bValue ? -1 : 1;
      return direction === 'asc' ? comparison : -comparison;
    });
  }, []);

  // Sidebar handlers
  const handleToggleSidebar = useCallback(() => {
    setIsSidebarOpen(prev => !prev);
  }, []);

  // Filter change handler
  const handleFilterChange = useCallback((newFilters) => {
    setFilters(prevFilters => {
      const updatedFilters = { ...prevFilters, ...newFilters };
      const filteredResults = applyFilters(updatedFilters, expenseData);
      const sortedResults = applySorting(filteredResults, sortColumn, sortDirection);
      setFilteredData(sortedResults);
      setCurrentPage(1);
      return updatedFilters;
    });
  }, [applyFilters, applySorting, expenseData, sortColumn, sortDirection]);

  // Sort handler
  const handleSort = useCallback((column) => {
    setSortColumn(column);
    setSortDirection(prev => {
      const newDirection = prev === 'asc' ? 'desc' : 'asc';
      const sortedData = applySorting(filteredData, column, newDirection);
      setFilteredData(sortedData);
      return newDirection;
    });
  }, [applySorting, filteredData]);

  // Pagination handlers
  const handlePageChange = useCallback((page) => {
    setCurrentPage(page);
  }, []);

  const handlePageSizeChange = useCallback((newSize) => {
    setPageSize(newSize);
    setCurrentPage(1);
  }, []);

  // Data fetching effect
  useEffect(() => {
    const initializeData = async () => {
      try {
        setLoading(true);
        setError(null);
        const data = await fetchExpenseData();
        
        if (Array.isArray(data)) {
          const sortedData = applySorting(data, sortColumn, sortDirection);
          setExpenseData(data);
          setFilteredData(sortedData);
        } else {
          throw new Error('Invalid data format received');
        }
      } catch (err) {
        setError(err.message || 'Error loading data');
        setExpenseData([]);
        setFilteredData([]);
      } finally {
        setLoading(false);
      }
    };

    initializeData();
  }, [applySorting, sortColumn, sortDirection]);

  // Error component
  const ErrorDisplay = useMemo(() => (
    <div className="min-h-screen flex items-center justify-center">
      <div className="text-center p-4">
        <h2 className="text-xl font-semibold text-red-600 mb-2">Error Loading Data</h2>
        <p className="text-gray-600">{error}</p>
        <button 
          onClick={() => window.location.reload()}
          className="mt-4 px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition-colors"
        >
          Retry
        </button>
      </div>
    </div>
  ), [error]);

  // Loading state
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <LoadingSpinner />
      </div>
    );
  }

  // Error state
  if (error) {
    return ErrorDisplay;
  }

  // Main render
  return (
    <ErrorBoundary>
      <div className="min-h-screen bg-gray-50">
        <Header 
          onToggleSidebar={handleToggleSidebar}
        />
        <div className="flex min-h-screen pt-16">
          <Sidebar 
            isOpen={isSidebarOpen}
            onClose={() => setIsSidebarOpen(false)}
            filters={filters}
            onFilterChange={handleFilterChange}
            data={expenseData}
          />
          <MainContent 
            data={filteredData}
            currentPage={currentPage}
            pageSize={pageSize}
            sortColumn={sortColumn}
            sortDirection={sortDirection}
            onSort={handleSort}
            onPageChange={handlePageChange}
            onPageSizeChange={handlePageSizeChange}
            totalItems={filteredData.length}
          />
        </div>
      </div>
    </ErrorBoundary>
  );
};

export default App;