// src/components/Stats/StatCard.js
import React from 'react';
import { formatCurrency } from '../../utils/formatUtils';

export const StatCard = ({ icon, title, value, stats }) => {
  return (
    <div className="card-stats bg-white dark:bg-gray-800 rounded-xl shadow-lg p-6">
      <div className="flex items-center">
        <div className={`p-3 rounded-full ${icon.background}`}>
          <i className={`${icon.class} ${icon.color}`} />
        </div>
        <div className="ml-4">
          <p className="text-sm font-medium text-gray-500 dark:text-gray-400">
            {title}
          </p>
          <p className="text-xl font-semibold text-gray-900 dark:text-white">
            {typeof value === 'number' && title === 'Tổng chi phí' 
              ? formatCurrency(value)
              : value}
          </p>
        </div>
      </div>
      {stats && (
        <div className="mt-4 space-y-2">
          {stats.map((stat, index) => (
            <div
              key={index}
              className="flex justify-between items-center text-sm"
            >
              <span className="text-gray-600 dark:text-gray-400 truncate" title={stat.label}>
                {stat.label}
              </span>
              <span className="text-gray-900 dark:text-gray-100 font-medium">
                {typeof stat.value === 'number' ? formatCurrency(stat.value) : stat.value}
              </span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};