// src/components/Filters/CategoryFilter.js
import React, { useState } from 'react';

export const CategoryFilter = ({ title, name, data = [], selected = [], onChange }) => {
  const [searchTerm, setSearchTerm] = useState('');

  const filteredData = data.filter((item) =>
    item?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleSelectAll = (e) => {
    onChange(e.target.checked ? filteredData : []);
  };

  const handleItemChange = (item) => {
    const newSelected = selected.includes(item)
      ? selected.filter(i => i !== item)
      : [...selected, item];
    onChange(newSelected);
  };

  return (
    <div className="filter-group p-4 rounded-lg border border-gray-200 dark:border-gray-700">
      <div className="flex items-center justify-between mb-3">
        <label className="text-sm font-medium text-gray-700 dark:text-gray-300">
          {title}
        </label>
        <span className="text-xs bg-indigo-100 dark:bg-indigo-900 text-indigo-600 dark:text-indigo-400 px-2 py-1 rounded-full">
          {selected.length} đã chọn
        </span>
      </div>

      <div className="relative">
        <input
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full bg-gray-50 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-lg py-2 pl-9 pr-3 text-sm"
          placeholder={`Tìm kiếm ${title.toLowerCase()}...`}
        />
        <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" />
      </div>

      <div className="mt-2 max-h-40 overflow-y-auto space-y-1 bg-gray-50 dark:bg-gray-700 rounded-lg p-2">
        <label className="flex items-center space-x-2">
          <input
            type="checkbox"
            checked={selected.length === filteredData.length}
            onChange={handleSelectAll}
            className="rounded border-gray-300"
          />
          <span className="text-sm text-gray-600 dark:text-gray-400">
            Chọn tất cả
          </span>
        </label>

        {filteredData.map((item) => (
          <label key={item} className="flex items-center space-x-2">
            <input
              type="checkbox"
              checked={selected.includes(item)}
              onChange={() => handleItemChange(item)}
              className="rounded border-gray-300"
            />
            <span className="text-sm text-gray-600 dark:text-gray-400">
              {item}
            </span>
          </label>
        ))}
      </div>
    </div>
  );
};
