export const formatCurrency = (amount) => {
    if (!amount || isNaN(amount)) return '0 ₫';
    
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };
  
  const parseDateString = (dateString) => {
    if (!dateString) return null;
    
    try {
      if (dateString instanceof Date) {
        return dateString;
      }
  
      if (typeof dateString !== 'string') {
        return null;
      }
  
      if (dateString.includes('T')) {
        const date = new Date(dateString);
        return isNaN(date.getTime()) ? null : date;
      }
  
      if (dateString.includes('/')) {
        const [datePart, timePart] = dateString.split(' ');
        const [month, day, year] = datePart.split('/');
        
        if (timePart) {
          const [hours, minutes, seconds] = timePart.split(':');
          const date = new Date(year, month - 1, day, hours, minutes, seconds);
          return isNaN(date.getTime()) ? null : date;
        } else {
          const date = new Date(year, month - 1, day);
          return isNaN(date.getTime()) ? null : date;
        }
      }
  
      const date = new Date(dateString);
      return isNaN(date.getTime()) ? null : date;
  
    } catch {
      return null;
    }
  };
  
  export const formatDate = (dateString) => {
    if (!dateString) return '-';
    
    const date = parseDateString(dateString);
    if (!date) return '-';
    
    return new Intl.DateTimeFormat('vi-VN', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };
  
  export const formatDateForExport = (dateString) => {
    const date = parseDateString(dateString);
    if (!date) return '';
    
    return new Intl.DateTimeFormat('vi-VN', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    }).format(date);
  };
  
  export const parseDateRange = (fromDate, toDate) => {
    const from = fromDate ? parseDateString(fromDate) : null;
    const to = toDate ? parseDateString(toDate) : null;
    
    if (from) {
      from.setHours(0, 0, 0, 0);
    }
    
    if (to) {
      to.setHours(23, 59, 59, 999);
    }
    
    return { from, to };
  };
  
  export const isDateInRange = (dateString, fromDate, toDate) => {
    const date = parseDateString(dateString);
    if (!date) return false;
    
    const { from, to } = parseDateRange(fromDate, toDate);
    
    if (!from && !to) return true;
    if (from && !to) return date >= from;
    if (!from && to) return date <= to;
    return date >= from && date <= to;
  };