
// src/components/Stats/StatsContainer.js
import React from 'react';
import { StatCard } from './StatCard';

export const StatsContainer = ({ data }) => {
  const calculateStats = () => {
    const totalAmount = data.reduce((sum, item) => sum + parseFloat(item["SỐ TIỀN"]), 0);
    
    const areaStats = data.reduce((acc, item) => {
      const area = item["KHU VỰC"];
      if (!acc[area]) acc[area] = { count: 0, amount: 0 };
      acc[area].count++;
      acc[area].amount += parseFloat(item["SỐ TIỀN"]);
      return acc;
    }, {});

    const projectStats = data.reduce((acc, item) => {
      const project = item["Dự án"];
      if (!acc[project]) acc[project] = { count: 0, amount: 0 };
      acc[project].count++;
      acc[project].amount += parseFloat(item["SỐ TIỀN"]);
      return acc;
    }, {});

    const expenseTypeStats = data.reduce((acc, item) => {
      const type = item["Mã CHI"];
      if (!acc[type]) acc[type] = { count: 0, amount: 0 };
      acc[type].count++;
      acc[type].amount += parseFloat(item["SỐ TIỀN"]);
      return acc;
    }, {});

    return {
      totalAmount,
      areaStats: Object.entries(areaStats)
        .sort((a, b) => b[1].amount - a[1].amount)
        .slice(0, 3)
        .map(([label, { amount }]) => ({ label, value: amount })),
      projectStats: Object.entries(projectStats)
        .sort((a, b) => b[1].amount - a[1].amount)
        .slice(0, 3)
        .map(([label, { amount }]) => ({ label, value: amount })),
      expenseTypeStats: Object.entries(expenseTypeStats)
        .sort((a, b) => b[1].amount - a[1].amount)
        .slice(0, 3)
        .map(([label, { amount }]) => ({ label, value: amount }))
    };
  };

  const stats = calculateStats();

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-6">
      <StatCard
        icon={{
          class: 'fas fa-money-bill',
          color: 'text-indigo-600 dark:text-indigo-400',
          background: 'bg-indigo-100 dark:bg-indigo-900'
        }}
        title="Tổng chi phí"
        value={stats.totalAmount}
      />

      <StatCard
        icon={{
          class: 'fas fa-map-marker',
          color: 'text-blue-600 dark:text-blue-400',
          background: 'bg-blue-100 dark:bg-blue-900'
        }}
        title="Khu vực"
        value={Object.keys(stats.areaStats).length}
        stats={stats.areaStats}
      />

      <StatCard
        icon={{
          class: 'fas fa-project-diagram',
          color: 'text-green-600 dark:text-green-400',
          background: 'bg-green-100 dark:bg-green-900'
        }}
        title="Dự án"
        value={Object.keys(stats.projectStats).length}
        stats={stats.projectStats}
      />

      <StatCard
        icon={{
          class: 'fas fa-tags',
          color: 'text-purple-600 dark:text-purple-400',
          background: 'bg-purple-100 dark:bg-purple-900'
        }}
        title="Loại chi phí"
        value={Object.keys(stats.expenseTypeStats).length}
        stats={stats.expenseTypeStats}
      />
    </div>
  );
};